.share-passwords-page .password-sharing .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:5% 0;
    padding:0 10%;
    .col1{
        width: 37.5%;
    }
    .col2{
        width: 42.5%;
        position: relative;
    }
    h1{
        font-size: 33px;
        margin-bottom: 20px;
    }
    p{
        font-size: 18px;
    }
    .top-img{
        content: url('../../../../assets/SharePasswords/img1.png');
        width: 75%;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 50%;
        transition: 0.35s ease-in-out;
        &:hover{
            border-radius: 10%;
        }
    }
    .bg-img{
        content: url('../../../../assets/SharePasswords/bg1.png');
        width: 100%;
    }
}

@media(max-width:1401px){
    .share-passwords-page .password-sharing .row1{
        margin:5% 0;
        padding:0 10%;
        .col1{
            width: 40%;
        }
        .col2{
            width: 42.5%;
        }
        h1{
            font-size: 25px;
            margin-bottom: 20px;
        }
        p{
            font-size: 15px;
        }
        .top-img{
            width: 75%;
        }
    }
}

@media(max-width:767px){
    .share-passwords-page .password-sharing .row1{
        flex-wrap: wrap;
        margin:30px 0;
        padding:0 7.5%;
        .col1{
            width: 400px;
            max-width: 100%;
            margin:25px auto 15px;
        }
        .col2{
            width: 400px;
            max-width: 100%;
            margin:15px auto;
        }
        h1{
            font-size: 20px;
            margin-bottom: 15px;
        }
        .top-img{
            width: 75%;
        }
    }
}

//===========================================
.share-passwords-page .password-sharing .row2{
    background: url('../../../../assets/SharePasswords/row-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:8% 20%;
    clip-path: polygon(0 0, 100% 17.5%, 100% 100%, 0 87.5%);
    text-align: center;
    color:#fff;
    .icon{
        content: url('../../../../assets/SharePasswords/icon1.png');
        width: 75px;
        margin:0 auto;
    }
    h2{
        font-size: 33px;
        margin:35px 0;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .share-passwords-page .password-sharing .row2{
        padding:8.5% 20%;
        clip-path: polygon(0 0, 100% 10%, 100% 100%, 0 90%);
        .icon{
            width: 60px;
        }
        h2{
            font-size: 25px;
            margin:30px 0;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:767px){
    .share-passwords-page .password-sharing .row2{
        padding:15% 7.5%;
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
        .icon{
            width: 50px;
        }
        h2{
            font-size: 20px;
            margin:25px 0;
        }
    }
}


//=========================================
.share-passwords-page .password-sharing .row3{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:5% 0 7.5%;
    padding:0 7.5%;
    .img{
        content: url('../../../../assets/SharePasswords/img2.png');
        width: 50%;
    }
    .info{
        width: 40%;
    }
    h1{
        font-size: 35px;
        margin-bottom: 15px;
    }
    p{
        font-size: 18px;
        margin:15px 0;
    }
}

@media(max-width:1401px){
    .share-passwords-page .password-sharing .row3{
        .img{
            width: 47.5%;
        }
        .info{
            width: 40%;
        }
        h1{
            font-size: 27px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
            margin:15px 0;
        }
    }
}

@media(max-width:790px){
    .share-passwords-page .password-sharing .row3{
        margin:30px 0;
        flex-wrap: wrap;
        .img{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        .info{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 20px;
            margin-bottom: 10px;
        }
        p{
            margin:10px 0;
        }
    }
}