.share-passwords-page{
    overflow: hidden;
}
.share-passwords-page .shared-banner{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 65% 80%, 0 100%);
    position: relative;
    overflow:visible;
    z-index: 1;
    h1{
        font-size: 42px;
    }
}

.share-passwords-page .shared-banner .other-elements{
    width: 22.5%;
    position: absolute;
    right: 15%;
    bottom:17.5%;
    .banner-img{
        width: 100%;
        border-radius: 10px;
        background-color: #fff;
        padding:40px 25px;
        &:hover{
            button{
                background-color: #2458d1;
                transform: scale(1.1);
            }
        }
    }
    .icons{
        color:#427CFF;
        font-size: 37.5px;
        display: flex;
        align-items: center;
        justify-content: center;
        svg{
            margin:0 3.5px;
        }
        
    }
    h6{
        font-size: 25px;
        font-weight: normal;
        color:#111;
        text-align: center;
        margin:20px 0;
    }
    small{
        font-size: 18px;
        color:#111;
        display: flex;
        justify-content: center;
        margin-bottom: 40px;
        
    }
    .profiles{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .profile1{
        content: url('../../../../assets/SharePasswords/profile1.png');
        width: 55px;
        border-radius: 50%;
        z-index: 3;
    }
    .profile2{
        content: url('../../../../assets/SharePasswords/profile2.png');
        width: 55px;
        border-radius: 50%;
        margin-left: -15px;
        z-index: 2;
    }
    .profile3{
        content: url('../../../../assets/SharePasswords/profile3.png');
        width: 55px;
        border-radius: 50%;
        margin-left: -22.5px;
    }
    button{
        background-color: #427CFF;
        color:#fff;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 55px;
        height: 55px;
        border-radius: 50%;
        border: 0;
        font-size: 25px;
        margin-left: 5px;
        cursor: pointer;
        transition: 0.3s ease-in-out;
    }
}

@media(max-width:1401px){
    .share-passwords-page .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 65% 85%, 0 100%);
        h1{
            font-size: 27.5px;
        }
    }
    
    .share-passwords-page .shared-banner .other-elements{
        width: 25%;
        right: 15%;
        bottom:12.5%;
        .banner-img{
            padding:30px 20px;
        }
        .icons{
            font-size: 30px;
            svg{
                margin:0 3px;
            }
            
        }
        h6{
            font-size: 20px;
            margin:15px 0;
        }
        small{
            font-size: 15px;
            margin-bottom: 30px;
            
        }
        .profile1{
            width: 47.5px;
        }
        .profile2{
            width: 47.5px;
            margin-left: -15px;
        }
        .profile3{
            width: 47.5px;
            margin-left: -22.5px;
        }
        button{
            width: 47.5px;
            height: 47.5px;
            font-size: 22.5px;
        }
    }
}

@media(max-width:767px){
    .share-passwords-page .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 75% 95%, 0 100%);
        h1{
            font-size: 20px;
        }
    }
    
    .share-passwords-page .shared-banner .other-elements{
        position: relative;
        width: 25%;
        right: unset;
        bottom:unset;
        width: 350px;
        max-width: 100%;
        margin:35px auto  0;
        .icons{
            font-size: 27.5px;
        }
        .profile1{
            width: 45px;
        }
        .profile2{
            width: 45px;
            margin-left: -13px;
        }
        .profile3{
            width: 45px;
            margin-left: -20px;
        }
        button{
            width: 45px;
            height: 45px;
        }
    }
}